body {
  background: #0a0a1a url('https://img.freepik.com/free-photo/retro-digital-art-illustration-person-using-radio-technology_23-2151355979.jpg?t=st=1758026477~exp=1758030077~hmac=d8dd00ce881e4f47d097612491c0f74f58830f68f61036c1d2180ae6d4f1cc18&w=1480') no-repeat center center fixed;
  background-size: cover;
  color: #F7FAFA;
  font-family: 'Orbitron', 'Inter', 'Arial', sans-serif;
  margin: 0;
  padding: 0;
  min-height: 100vh;
  /* Add a subtle overlay for readability */
}

body::before {
  content: "";
  position: fixed;
  top: 0; left: 0; right: 0; bottom: 0;
  background: linear-gradient(135deg, rgba(20, 10, 50, 0.45), rgba(10, 10, 30, 0.35) 70%);
  z-index: 0;
  pointer-events: none;
}

/* Neon-glow animated HERO section */
.hero {
  text-align: center;
  margin-bottom: 2em;
  padding-bottom: 1em;
  border-bottom: 2px solid #3217e6;
  position: relative;
  z-index: 1;
  animation: textFadeIn 1.4s cubic-bezier(0.23, 1, 0.32, 1) both;
}

@keyframes textFadeIn {
  from { opacity: 0; transform: translateY(-30px) scale(0.93);}
  to { opacity: 1; transform: translateY(0) scale(1);}
}

.hero h1 {
  font-size: 3em;
  background: linear-gradient(90deg, #ff00cc 30%, #00d8ff 70%, #fff 100%);
  background-clip: text;
  -webkit-background-clip: text;
  color: transparent;
  filter: drop-shadow(0 0 18px #00f6ffcc);
  letter-spacing: 2px;
  animation: neonGlow 1.5s infinite alternate;
}

@keyframes neonGlow {
  0% { filter: drop-shadow(0 0 8px #00f6ffcc);}
  100% { filter: drop-shadow(0 0 22px #ff00cc); }
}

.hero h2 {
  color: #fcff4b;
  font-size: 1.5em;
  font-weight: 700;
  margin: 0.5em 0 0.5em;
  text-shadow: 0 0 10px #ff0099, 0 0 2px #fff;
  letter-spacing: 1.1px;
  animation: flicker 4s infinite alternate;
}
@keyframes flicker {
  0% { opacity: 0.85; text-shadow: 0 0 16px #ff0099, 0 0 4px #fff;}
  100% { opacity: 1; text-shadow: 0 0 28px #fff, 0 0 8px #00d8ff;}
}

.hero .contact a {
  color: #00d8ff;
  margin: 0 14px;
  font-size: 2.1em;
  transition: color 0.18s, transform 0.21s;
  filter: drop-shadow(0 0 5px #00d8ff88);
}
.hero .contact a:hover {
  color: #fcff4b;
  transform: scale(1.18) rotate(-7deg);
}

.hero .gamepad {
  color: #fff71b;
  margin-bottom: 16px;
  animation: spin 2.3s linear infinite;
  filter: drop-shadow(0 0 20px #fcff4b99);
}
@keyframes spin {
  0% {transform: rotate(0deg);}
  100% {transform: rotate(360deg);}
}

.hero p {
  color: #fff;
  font-size: 1.12em;
  margin-top: 0.6em;
  text-shadow: 0 0 7px #000;
}

.container {
  max-width: 1000px;
  margin: 100px auto;
  background: rgba(16, 18, 48, 0.70);
  border-radius: 45px;
  box-shadow: 0 0 45px #08082a99;
  padding: 45px 50px;
  position: relative;
  z-index: 1;
  overflow: hidden;
}

/* Neon animated border for cards */
.card {
  background: rgba(24, 24, 48, 0.99);
  border-radius: 15px;
  margin-bottom: 2.2em;
  padding: 2em 2em 1.6em 2em;
  box-shadow: 0 0 30px #00d8ff33, 0 0 1px #fff;
  border: 2.5px solid transparent;
  border-image: linear-gradient(120deg, #00d8ff, #ff00cc, #fcff4b 80%) 1;
  position: relative;
  transition: transform 0.18s cubic-bezier(0.25,0.9,0.4,1.2), box-shadow 0.18s, border-color 0.18s;
  z-index: 2;
  will-change: transform, box-shadow;
}

.card:hover {
  transform: scale(1.035);
  box-shadow: 0 0 70px #00d8ff99, 0 0 22px #ff00cc99, 0 0 4px #fff;
  border-color: #ff00cc;
  animation: popUp 1s;
}

@keyframes popUp {
  0% { transform: scale(1) rotate(0deg);}
  60% { transform: scale(1.09) rotate(0deg);}
  100% { transform: scale(1.035) rotate(0deg);}
}

.card h3 {
  color: #fcff4b;
  font-size: 1.4em;
  font-family: 'Orbitron', 'Inter', sans-serif;
  margin-bottom: 0.7em;
  letter-spacing: 1px;
  animation: sectionTitleGlow 2.2s infinite alternate;
}

@keyframes sectionTitleGlow {
  0% {text-shadow: 0 0 10px #00d8ff;}
  100% {text-shadow: 0 0 22px #ff00cc;}
}

/* Animated underline on section headings */
.card h3::after {
  content: '';
  display: block;
  margin: 9px auto 0 auto;
  width: 60px;
  height: 4px;
  background: linear-gradient(90deg, #00d8ff 30%, #ff00cc 70%, #fcff4b 100%);
  border-radius: 2px;
  box-shadow: 0 0 8px #ff00cc;
  opacity: 0.8;
  animation: underlineGlow 2.5s infinite alternate;
}
@keyframes underlineGlow {
  0% { box-shadow: 0 0 8px #00d8ff;}
  100% { box-shadow: 0 0 18px #ff00cc;}
}

ul, .timeline {
  margin: 0.5em 0 0.5em 1em;
  padding: 0;
}

li {
  margin-bottom: 0.5em;
}

.timeline .job {
  margin-bottom: 2.1em;
  animation: fadeInSection 1.2s cubic-bezier(0.23, 1, 0.32, 1) both;
}
.timeline .job h4, .projects .project h4 {
  color: #00d8ff;
  margin-bottom: 0.2em;
  font-size: 1.12em;
  letter-spacing: 1px;
  filter: drop-shadow(0 0 12px #00d8ff99);
}
.timeline .job span {
  color: #fcff4b;
  font-size: 0.98em;
  margin-bottom: 0.6em;
  display: block;
  text-shadow: 0 0 4px #000;
}
.project h4 {
  color: #ff00cc;
  filter: drop-shadow(0 0 8px #ff00cc99);
  text-shadow: 0 0 8px #ff00cc99, 0 0 2px #fff;
}
.projects .project {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  /* Keep your existing styles */
  margin-bottom: 1.5em;
  padding-bottom: 1em;
  border-bottom: 1px solid #2b2e42;
  animation: fadeInSection 1.1s cubic-bezier(0.23, 1, 0.32, 1) both;
  transition: box-shadow 0.2s, transform 0.18s;
}
.projects .project:last-child {
  border-bottom: none;
}
.projects .project:hover {
  box-shadow: 0 0 16px #ff00cc80, 0 0 8px #fcff4b50;
  transform: scale(1.03) rotate(0.6deg);
}
/* Optionally, to ensure no filter on hover: */
.projects .project:hover h4,
.projects .project:hover p {
  filter: none !important;
}

@keyframes fadeInSection {
  from { opacity: 0; transform: scale(0.97) translateY(32px);}
  to { opacity: 1; transform: scale(1) translateY(0);}
}

.game-credit {
  color: #00d8ff;
  font-weight: bold;
  letter-spacing: 1px;
  margin-left: 1em;
  text-shadow: 0 0 8px #00f6ff99;
}

/* Add glowing effect to skills */
.skills li {
  background: linear-gradient(90deg,#00d8ff11 0%, #ff00cc11 100%);
  border-radius: 8px;
  padding: 0.25em 0.6em;
  margin-bottom: 0.45em;
  box-shadow: 0 0 8px #00d8ff33;
  transition: background 0.21s, box-shadow 0.21s;
}
.skills li:hover {
  background: linear-gradient(90deg, #00d8ff88 0%, #ff00cc88 100%);
  box-shadow: 0 0 18px #ff00cc88;
  color: #fcff4b;
}

footer {
  text-align: center;
  margin: 2em 0 0 0;
  color: #aaa;
  font-size: 1.07em;
  letter-spacing: 1px;
  text-shadow: 0 0 4px #000;
}

@media (max-width: 600px) {
  .container {
    padding: 12px 2px;
  }
  .card {
    padding: 1em 0.5em;
  }
  .hero h1 {
    font-size: 2em;
  }
}